
.github{
    margin-bottom: 3rem;
    background-color: var(--color-even-section-bg);
}
.github_stats{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 0.5rem;
    margin-bottom: 2rem;
}
.github_calendar{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.github_calendar > h2{
    text-align: center;
    color: var(--color-primary);
  margin-bottom: 3rem;
}

/* ============== MEDIUM DEVICES ============== */
@media screen and (max-width:1024px){
    
  }
  
  /* ============== SMALL DEVICES ============== */
  @media screen and (max-width:600px){
    .github_stats{
        grid-template-columns: repeat(1,1fr);
        gap: 0.8rem;
    }

  }