.loader{
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader-content{
    margin-top: 14rem;
    width: 30%;
}

/* ============== MEDIUM DEVICES ============== */
@media screen and (max-width:1024px){
    .loader-content{
        margin-top: 14rem;
        width: 80%;
    }
  }
  
  /* ============== SMALL DEVICES ============== */
  @media screen and (max-width:600px){
    .loader-content{
        margin-top: 14rem;
        width: 80%;
    }

  }