nav{
    width: max-content;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    top: 1rem;
    display: flex;
    gap: 2.5rem;
    background: var(--color-nav-bg);
    border-radius: 16px;
    /* box-shadow: 0 1px 20px rgba(0, 0, 0, 0.1); */
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(6.2px);
    border: 1px solid var(--color-nav-border);
    align-items: center;
}
.navlink{
    width: max-content;
    display: flex;
    gap: 3rem;
}
.navlink button{
    width: max-content;
    background: none;
    position: relative;
    padding-bottom: 0.2rem;
}
.navlink button a::before{
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: var(--border-size);
    background-color: var(--color-primary);
    transform: scaleX(0);
    transition: transform 300ms ease-in-out;
}
.navlink button a:hover::before{
    transform: scaleX(1);
}
.nav-active > a{

    color: var(--color-text);
}
.nav-active::before{
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: var(--border-size);
    background-color: var(--color-primary);
}

.navbar-right-part{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.navbar-right-part > button,.navbar-right-part > li{
    display: flex;
    align-items: center;
    cursor: pointer;
}

nav a{
    color: var(--color-light);
    font-size: 1rem;
}
.toggleMenuBtn{
    background: none;
    color: var(--color-text);
    cursor: pointer;
    font-size: 1.2rem;
}
.navbar-right-part > button{
    display: none;
}
.logo{
    height: 25px;
    width: 25px;
    object-fit: cover
}

  /* ============== SMALL DEVICES ============== */
  @media screen and (max-width:600px){
    nav{
        align-items: flex-start;
        width: 330px;
        justify-content: space-between;
    }
    .navlink{
        padding-top: 2.2rem;
        flex-direction: column;
        align-items: center;
        display: none;
    }
    /* .navbar-right-part{
        margin-top: 4px;
    } */
    .navbar-right-part > button{
        display: flex;
    }
    .navlink.active{
        padding-top: 2.2rem;
        flex-direction: column;
        display: flex;
    }
  }