header{
    height: 400px;
    padding-top: 8rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    /* height: 100%; */
    position: relative;
}
.header_container > h5{
    font-size: 1.2rem;
}
.header_container > h1{
    font-size: 4rem;
}

/* ===================== CTA ===================== */

.cta{
    margin-top: 1.1rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/* ===================== Header Socials ===================== */

.header_social{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    position: absolute;
    left: 0;
    top: 3rem;
}
.bottom_header_social{
    display: none;
    align-items: center;
    gap: 2rem;
    margin-top: 1rem;
    justify-content: center;
}
.header_social a:hover,.bottom_header_social a:hover{
    transform: scale(1.8);
}
.circle-Head{
    margin-top: 1.1rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
}
.ping1 {
    position: absolute;
    top: 0;
    border-radius: 50%;
    border: 1px solid var(--circle-color);
    height: 120px;
    width: 120px;
    z-index: -1;
    animation: ping1 2.5s ease-out infinite;
  }
  @keyframes ping1 {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(3);
      opacity: 0;
    }
  }
/* .header_social::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
} */

/* ===================== Image ===================== */

.me{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 3rem;
    border-radius: 12rem;
    overflow: hidden;
    opacity: 0.8;
}

/* ===================== scroll down ===================== */

.scroll_down{
    position: absolute;
    right: -2.3rem;
    top: 5rem;
    transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}

/* ============== MEDIUM DEVICES ============== */
@media screen and (max-width:1024px){
    /* header{
        height: 68vh;
    } */
  }
  
  /* ============== SMALL DEVICES ============== */
  @media screen and (max-width:600px){
    /* header{
        height: 100vh;
    } */
    .header_social,.scroll_down{
        display: none;
    }
    .header_container > h1{
        font-size: 3rem;
    }
    header{
        height: 418px;
    }
    .bottom_header_social{
        display: flex;
    }
