.SkillContainer{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    padding: 1.1rem;
    gap: 0.5rem;
    align-items: center;
    box-shadow: 0 1px 20px rgba(0, 0, 0, 0.1);
    border: 1px solid var(--color-nav-border);
    border-radius: 1.5rem;
    justify-content: center;
}
.skillsCard{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.skillsCard_img{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 120px;
    border-radius: 1.5rem;
    /* overflow: hidden; */
    background-color: white;
}
.skillsCard_img img{
    width: 60px;
    height: 60px;
    object-fit: cover;
}

